<template>
  <div class="sitemap">
    <h1>Sitemap</h1>
    <img alt="Sitemap" src="../assets/sitemap.png" style="width:100%" />
    {{ Sitemap }}
  </div>
</template>

<script>
import { fs } from "fs";

export default {
  setup() {
    let sitemap = "";
    fs.readFile("@/sitemap.xml", data => {
      sitemap = data;
    });
    return { sitemap };
  },
};
</script>
